<template>
  <div>
    <div id="box1" class="box">
      <ul>
        <li>
          <el-input v-model="input" placeholder="课程ID/名称"></el-input>
        </li>
        <li>
          <el-select v-model="value" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </li>
        <li>
          <el-button type="primary">查询</el-button>
        </li>
        <li>
          <el-button>重置</el-button>
        </li>
      </ul>
    </div>
    <div id="box2" class="box">
      <div>
        <el-button type="primary">+添加真题</el-button>
      </div>
      <div id="table">
        <el-table
          ref="multipleTable"
          :data="tableData"
          tooltip-effect="dark"
          style="width: 100%"
          @selection-change="handleSelectionChange"
        >
          <el-table-column type="selection" width="55"></el-table-column>
          <el-table-column label="历年真题ID" width="340" prop="classId"></el-table-column>
          <el-table-column prop="className" label="真题年份" width="290"></el-table-column>
          <el-table-column prop="addTime" label="添加时间" show-overflow-tooltip></el-table-column>
          <el-table-column fixed="right" label="操作" width="120">
            <template slot-scope="scope">
              <el-button
                @click.native.prevent="deleteRow(scope.$index, tableData)"
                type="text"
                size="small"
              >移除</el-button>
                <el-button @click="handleClick(scope.row)" type="text" size="small">查看</el-button>
        <el-button type="text" size="small">编辑</el-button>
            </template>
          </el-table-column>
        </el-table>
        <div id="page">
          <el-pagination background layout="prev, pager, next" :total="1000"></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "cont1",

  data() {
    return {
      input: "",

      options: [
        {
          value: "选项1",
          label: "2019年"
        },
        {
          value: "选项2",
          label: "2018年"
        },
        {
          value: "选项3",
          label: "2020年"
        }
      ],
      tableData: [
        {
          classId: "22222",
          className: "2019年",
          addTime: "2017-07-19 14:48:38"
        },   {
          classId: "22222",
          className: "2019年",
          addTime: "2017-07-19 14:48:38"
        },   {
          classId: "22222",
          className: "2019年",
          addTime: "2017-07-19 14:48:38"
        },   {
          classId: "22222",
          className: "2019年",
          addTime: "2017-07-19 14:48:38"
        },   {
          classId: "22222",
          className: "2019年",
          addTime: "2017-07-19 14:48:38"
        },
       
      ],

      value: "",
      multipleSelection: []
    };
  },
  methods: {
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    deleteRow(index, rows) {
      rows.splice(index, 1);
    },    handleClick(row) {
        console.log(row);
      }
  }
};
</script>
<style>
.box {
  padding: 20px 10px;
  width: 90%;
  margin: 20px auto;
  background: white;
  border-radius: 5px;
}
#box1 {
}
#box1 ul li {
  margin-left: 20px;
  list-style: none;
  display: inline-block;
}
#box2::after {
  display: block;
  content: "";
  clear: both;
}
#page {
  margin-top: 20px;
  float: right;
}
</style>